import React, { FC } from 'react';
import { useSelector } from 'react-redux';
import {
  cnpjFormatter,
  ColumnsProps,
  DefaultSortProps,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMFP } from '../../../../dependencies/empresaMFPDependency';
import { CLASS_NAME, PrevidenciaComplementar } from '../../../../models/dtos/mfp/previdenciaComplementar';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'codigo',
    order: OrderType.ASC,
  },
];

const PrevidenciaComplementarSearchView: FC<SearchViewProps<PrevidenciaComplementar>> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const columns: ColumnsProps[] = [
    {
      dataField: 'codigo',
      editable: true,
      filter: {
        type: FilterType.NUMBER,
      },
      sort: true,
      text: 'Código',
    },
    {
      dataField: 'razaoSocial',
      editable: true,
      sort: true,
      text: 'Razão Social',
    },
    {
      dataField: 'cnpj',
      editable: false,
      formatter: cnpjFormatter,
      sort: false,
      text: 'CNPJ',
    },
  ];

  return (
    <WindowComponent
      title="Manutenção de Informações da Previdência Complementar"
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMFP(globalReducer.globalParameter)]}
    >
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        columns={columns}
        defaultSorted={defaultSorted}
        dtoClassName={CLASS_NAME}
        extraData={getExtraData(props, 'PrevidenciaComplementarEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default PrevidenciaComplementarSearchView;
